.solarHeatTile {
  height: 620px;
  width: 920px;
  border-radius: 10px;
  background: rgb(7, 1, 94);
  background: linear-gradient(90deg, rgb(21,63,137) 0%, rgb(97, 150, 200) 50%, rgba(21,63,137) 100%);
  margin: 5px;
  position: relative;
}

.button-solarHeat {
  background-image: radial-gradient(100% 100% at 100% 0, #5b8fdc 0, #07235c 100%);
  /* background: #6e28ca; */
  border-radius: 999px;
  box-shadow: #2b6ccd 0 10px 20px -10px;
  box-sizing: border-box;
  color: #e9e8e8;
  cursor: pointer;
  font-family: Inter, Helvetica, "Apple Color Emoji", "Segoe UI Emoji", NotoColorEmoji, "Noto Color Emoji", "Segoe UI Symbol", "Android Emoji", EmojiSymbols, -apple-system, system-ui, "Segoe UI", Roboto, "Helvetica Neue", "Noto Sans", sans-serif;
  font-size: 16px;
  font-weight: 600;
  line-height: 16px;
  opacity: 1;
  outline: 0 solid transparent;
  padding: 12px 16px;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  width: fit-content;
  word-break: break-word;
  border: 0;
}

.button-solarHeat:hover {
  background-image: radial-gradient(100% 100% at 100% 0, #326fca 0, #133e7f 100%);
}

.theme-solar2 {
  background: rgb(7, 1, 94);
  background: linear-gradient(90deg, rgb(21,63,137) 0%, rgb(97, 150, 200) 50%, rgba(21,63,137) 100%);
}
