.theme-hydro {
  background: rgb(7, 1, 94);
  background: linear-gradient(90deg, rgb(52, 159, 199) 0%, rgb(181, 203, 226) 50%, rgba(37, 150, 190) 100%);
}

.button-hydro {
  background-image: radial-gradient(100% 100% at 100% 0, #76a2e4 0, #2b6ccd 100%);
  box-shadow: #2b6ccd 0 10px 20px -10px;
}

.button-hydro:hover {
  background-image: radial-gradient(100% 100% at 100% 0, #2b6ccd 0, #2b6ccd 100%);
}

.variable-header {
  color: #fffefe;
  width: 146px;
  height: 62px;
  font-family: arial;
  font-weight: bold;
  font-size: 19px;
  margin: auto;
  /* text-align: ; */
  position: absolute;
  top: 40px;
  left: 30px;
}

.wicketGateContainer {
  width: 90%;
  margin: 0px;
  display: flex;
  /* justify-content: space-between; */
  /* align-items: center; */
}

.wicketGateContainerButton {
  flex: 0 0 auto;
  width: 30px;
  height: 30px;
  border-radius: 100%;
  background: white;
  font-size: 18px;
  border: 1px solid lightgrey;
  cursor: pointer;
  -webkit-appearance: none;
  margin: 0 0px;
}
