.mainTile {
  height: 620px;
  width: 920px;
  border-radius: 10px;
  background: rgb(7, 1, 94);
  background: linear-gradient(90deg,
      rgba(7, 1, 94, 1) 0%,
      rgba(104, 40, 133, 1) 50%,
      rgba(7, 1, 94, 1) 100%);
  margin: 5px;
  position: relative;
}

hr {
  border: none;
  border-top: 4px dotted #f00;
  color: #fff;
  height: 1px;
  width: 50%;
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.main-nav {
  margin-top: 5px;
  margin-left: 50px;
  list-style: none;
  display: "flex";
}

.logo {
  margin: 0;
}

.main-nav h3 {
  padding: 10px 15px;
  /* text-transform: uppercase; */
  text-align: center;
  display: block;
  text-decoration: none;
  color: aliceblue;
}

.arrow {
  border: solid #efea0a;
  border-width: 0 3px 3px 0;
  display: inline-block;
  padding: 8px;
}

.right {
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
}

.button-34 {
  background-image: radial-gradient(100% 100% at 100% 0,
      #ab7de9 0,
      #7d33de 100%);
  /* background: #6e28ca; */
  border-radius: 999px;
  box-shadow: #8038dd 0 10px 20px -10px;
  box-sizing: border-box;
  color: #e9e8e8;
  cursor: pointer;
  font-family: Inter, Helvetica, "Apple Color Emoji", "Segoe UI Emoji",
    NotoColorEmoji, "Noto Color Emoji", "Segoe UI Symbol", "Android Emoji",
    EmojiSymbols, -apple-system, system-ui, "Segoe UI", Roboto, "Helvetica Neue",
    "Noto Sans", sans-serif;
  font-size: 16px;
  font-weight: 600;
  line-height: 16px;
  opacity: 1;
  outline: 0 solid transparent;
  padding: 12px 16px;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  width: fit-content;
  word-break: break-word;
  border: 0;
}

.button-34:hover {
  background-image: radial-gradient(100% 100% at 100% 0,
      #b688f1 0,
      #823ae2 100%);
}

.vertical-center {
  margin: 0;
  position: absolute;
  top: 50%;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}

.center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.card {
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  max-width: 360px;
  border-radius: 5px;
  height: 270px;
  background-color: #ebf2f9;
  text-align: center;
  align-items: center;
  cursor: pointer;
}

.card-container-text {
  font-size: 1.2vw;
  margin: 10px 20px;
  /* color: #0178b4; */
}

.card-round-div {
  background-color: hsla(0, 0%, 100%, 0.8509803921568627);
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin-top: -50px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.card-round-div-img {
  height: 60px;
  width: auto;
}

.card:hover {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.card-container {
  padding: 10px 16px;
}

.card-main-img {
  width: 100%;
  border-radius: 5px 5px 0 0;
}

@media screen and (max-width: 991px) {
  .card-container-text {
    font-size: 2.5vw;
    margin: 10px 20px;
    /* color: #0178b4; */
  }
}

@media screen and (max-width: 767px) {
  .card-container-text {
    font-size: 3.5vw;
    margin: 10px 20px;
    /* color: #0178b4; */
  }
}

@media screen and (max-width: 480px) {
  .card-container-text {
    font-size: 4vw;
    margin: 10px 20px;
    /* color: #0178b4; */
  }

  .card {
    height: 240px;
  }

  .card-round-div {
    width: 80px;
    height: 80px;
    margin-top: -40px;
  }

  .card-round-div-img {
    height: 50px;
    width: auto;
  }
}

