.solarPVTile {
  height: 620px;
  width: 920px;
  border-radius: 10px;
  background: rgb(7, 1, 94);
  background: linear-gradient(90deg, rgb(21, 137, 125) 0%, rgb(96, 175, 175) 50%, rgba(21, 137, 125) 100%);
  margin: 5px;
  position: relative;
}

.button-solarPV {
  background-image: radial-gradient(100% 100% at 100% 0, #62a889 0, rgb(24, 119, 110) 100%);
  /* background: #6e28ca; */
  border-radius: 999px;
  box-shadow: #62a889 0 10px 20px -10px;
  box-sizing: border-box;
  color: #e9e8e8;
  cursor: pointer;
  font-family: Inter, Helvetica, "Apple Color Emoji", "Segoe UI Emoji", NotoColorEmoji, "Noto Color Emoji", "Segoe UI Symbol", "Android Emoji", EmojiSymbols, -apple-system, system-ui, "Segoe UI", Roboto, "Helvetica Neue", "Noto Sans", sans-serif;
  font-size: 16px;
  font-weight: 600;
  line-height: 16px;
  opacity: 1;
  outline: 0 solid transparent;
  padding: 12px 16px;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  width: fit-content;
  word-break: break-word;
  border: 0;
}

.button-solarPV:hover {
  background-image: radial-gradient(100% 100% at 100% 0, #7ac0a0 0, rgb(41, 136, 127) 100%);
}

.margin-left-30px{
  margin-left: 10px;
  margin-bottom: 20px;
}
.grid-container {
  display: grid;
  grid-template-columns: auto auto ;
}

.theme-circuit {
  background: linear-gradient(90deg, rgb(21, 137, 125) 0%, rgb(96, 175, 175) 50%, rgba(21, 137, 125) 100%);
}

.theme-circuits {
  background: linear-gradient(90deg, #eba133 0%, #c29b65 50%, #ffac33 100%);
}
